*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

:root {
  //our font, distance, and font : varaiables.
  // --page-padding: 3rem;

  --gray: #333333;
  --orange: #ff7500;
  --light-orange: #ff8f2e5d;
  --dark-orange: #e86c005d;
}

html {
  font-size: 62.5%; //1rem = 10px;
}

header {
  height: 100vh;
}

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  line-height: 1.7;
  box-sizing: border-box;
  color: var(--gray);
}
